<template>
    <v-row class="pa-4 px-6">
        <v-col cols="5">

            <user-model-select @getUser_Father="getUser"></user-model-select>
        </v-col>
        <v-col cols="7">

            <avatar-and-name></avatar-and-name>
        </v-col>

        <v-col cols="12" class="pt-0">

                <!-- 子类地方 -->

                <v-treeview
                    :items="items"
                    class="mt-3"
                >
                    <template v-slot:prepend="{ item, open }">
                        <v-icon :i="item">
                        {{ open ? 'mdi-folder-open' : 'mdi-folder' }}
                        </v-icon>
                    </template>
                    <template v-slot:append="{ item, open }">
                        <rb-tree-item :the_item="item" :o="open"></rb-tree-item>
                    </template>
                </v-treeview>

                <!-- 子类结束 -->

        </v-col>
    </v-row>

</template>

<script>
import getUserData from '@/apis/mock/user_data'

import bonus_recommend from '@/common/es/bonus/bonusApi/bonus_recommend'
import AvatarAndName from '@/components/User/AvatarAndName.vue'
import RbTreeItem from './RbTreeItem.vue'
import UserModelSelect from '@/components/Form/UserModelSelect.vue'

    export default {
        components: { AvatarAndName, RbTreeItem, UserModelSelect },
        name: '',
        data() {
            return {
                items: [ ]
            }
        },
        mounted() {
            this.getUser()
        },
        methods: {
            numResult(user, ratio) {
                let user_0 = null

                if (user == 0) {
                    user_0 = {
                        children: this.items,
                        buys: [ { id: 1, num: 1, pv: 60 } ],
                    }
                } else if (user == 1) {
                    user_0 = this.items[0]
                } else {
                    user_0 = this.items[1]
                }

                const { user_1, user_2, user_3 } = bonus_recommend.ser_users_from_tree(user_0)

                let res = bonus_recommend.doing(user_0, user_1, user_2, user_3)

                res = bonus_recommend.doingResult(res, ratio)
                
                return res
            },

            getUser(code = 's') {
                const name = 'recommended'
                this.items = getUserData(name, code)
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


