import common from './common'

// 判断是否 是新手
const jugde_new = function(user_item, name_field) {

    if (user_item[name_field]) {
        return true
    }
    return false
}

// 判断本月是否 购买
const judge_buy = function(user_item, name_field) {
    if (user_item[name_field].length > 0) {
        return true
    }
    return false
}

/*
    获取 PV 数量
*/
const getPv = function(user_item) {
    return common.numd_pv(user_item, 'buys')
}

const ser_users_from_tree = function(users,  name_field="children") {
    let user_3 = []
    let user_2 = []
    let user_1 = []

    if (common.judge_has_children(users, name_field)) {
        
        // 1代
        const child_one = users[name_field]
        child_one.map(e => {

            if (common.judge_has_children(e, name_field)) {

                // 2代
                const child_two = e[name_field]
                child_two.map(f => {

                    if (common.judge_has_children(f, name_field)) {
                        user_3.push(...f[name_field])
                    }
                })
                user_2.push(...child_two)
            }
            user_1.push(e)
        })

    }

    return { user_1, user_2, user_3 }
}

// 计算结果
const doingResult = function(res, ratios = [ 0.2, 0.1, 0.05 ]) {
    return [
        res[0] * ratios[0],
        res[1] * ratios[1],
        res[2] * ratios[2]
    ]
}

const doing = function(user_0, user_1, user_2, user_3) {

    let pv = 0
    let res = [ 0, 0, 0]

    // 0代
    pv = getPv(user_0)

    if (pv > 0.5) {

        // 1代
        user_1.map(e => {

            if (jugde_new(e, 'isNew') && judge_buy(e, 'buys')) {
                
                res[0] += getPv(e)
            }
        })
        // 2代
        user_2.map(e => {

            if (jugde_new(e, 'isNew') && judge_buy(e, 'buys')) {
                
                res[1] += getPv(e)
            }
        })
        // 3代
        user_3.map(e => {

            if (jugde_new(e, 'isNew') && judge_buy(e, 'buys')) {
                
                res[2] += getPv(e)
            }
        })
    }
    return res
}

export default {
    doing,
    ser_users_from_tree,
    doingResult
}


