import user_s_recommended from './S/user_s_recommended'
import user_s_activehouse from './S/user_s_activehouse'
import user_s_activelayer from './S/user_s_activelayer'

import user_e_recommended from './E/user_e_recommended'
import user_e_activehouse from './E/user_e_activehouse'
import user_e_activelayer from './E/user_e_activelayer'

const users = {
    'user_s_recommended': user_s_recommended,
    'user_s_activehouse': user_s_activehouse,
    'user_s_activelayer': user_s_activelayer,

    'user_e_recommended': user_e_recommended,
    'user_e_activehouse': user_e_activehouse,
    'user_e_activelayer': user_e_activelayer
}

const getUserData = function(name, code) {
    const key_name = 'user_' + code + '_' + name

    return users[key_name]
} 

export default getUserData


