<template>
   
    <div class="d-inline">

        <v-sheet class="d-inline">
            &nbsp;&nbsp;&nbsp;&nbsp;<span class="sub-header">本月买入：</span>

            <v-btn icon x-small @click="changeNum(true)">
                <v-icon>mdi-plus</v-icon>
            </v-btn>

            <span :class="{ 'orange--text': num_color }">&nbsp;{{ num }}&nbsp;(件)</span>

            <v-btn icon x-small @click="changeNum(false)">
                <v-icon>mdi-minus</v-icon>
            </v-btn>
            
            <div class="d-inline rbti-checkbox hand" @click="changeNew">
                <span class="sub-header">，</span>
                <v-icon 
                    :color="new_color"
                >
                    {{ newed ? 'mdi-check-circle' : 'mdi-checkbox-blank-circle-outline'  }}
                </v-icon>
                <span class="sub-header">是否是新人</span>
            </div>

        </v-sheet>
    </div>
</template>

<script>
    export default {
        name: '',
        props: {
            the_item: {
                type: Object
            }
        },
        name: '',
        data() {
            return {
                num: 0,
                newed: false
            }
        },
        mounted() {
            this.num = this.the_item.buys[0].num
            this.newed = this.the_item.isNew
        },
        computed: {
            num_color() {
                if (this.num > 0) {
                    return true
                }
                return false
            },
            new_color() {
                if (this.newed) {
                    return 'orange darken-3'
                }
                return 'lighten-3'
            }
        },
        methods: {
            changeNum(plus_or_minus) {

                if (plus_or_minus) {
                    this.num += 1
                } else {
                    if (this.num > 0) {
                        this.num -= 1
                    }
                }

                this.the_item.buys[0].num = this.num
            },
            changeNew() {
                this.newed = !this.newed
                this.the_item.isNew = this.newed

            }
        }
    }
</script>

<style lang="sass" scoped>

        
</style> 


