
// 判断是否 有 子类
const judge_has_children = function(user, name_field = 'children') {
    if ( name_field in user) { return true } return false }

// 计算购买总 Pv
const numd_pv = function(user_item, name_field) {
    let i = 0 
    user_item[name_field].map(e => { i += (e.num * e.pv); return e })
    return i
}

// 判断 PV 是否足够
const judge_pv = function(pv, limit_pv) { 
    if (pv >= limit_pv) { return true } return false }


export default {
    judge_has_children,
    numd_pv,
    judge_pv
}


