<template>
    <content-layout>
        <div slot="content">
            
            <recommend-bonus-tree ref="recommendBonusTree"></recommend-bonus-tree>
        </div>

        <div slot="option">

            <v-select :items="users" v-model="user" :label="'结算对象'" color="blue-grey"></v-select>
            <v-select :items="ratios" v-model="ratio" :label="'奖励率'" color="blue-grey"></v-select>
            
            <v-btn block @click="numResult"
                color="blue-grey white--text mt-4">立刻结算</v-btn>
        </div>

        <div slot="result">
            <div class="pl-4 res-text">
                <div class="">第一层总和：<strong class="orange--text">{{ res[0] }}PV</strong></div>
                <div class="">第二层总和：<strong class="orange--text">{{ res[1] }}PV</strong></div>
                <div class="">第三层总和：<strong class="orange--text">{{ res[2] }}PV</strong></div>
            </div>
        </div>
    </content-layout>
</template>

<script>

import ContentLayout from '@/components/Layout/ContentLayout.vue'
import RecommendBonusTree from './Inner/RecommendBonusTree.vue'
    export default {
        components: { ContentLayout, RecommendBonusTree },
        name: '',
        data() {
            return {
                user: 0,
                users: [ {
                    text: '第0代', value: 0
                }, {
                    text: '第1代A', value: 1
                }, {
                    text: '第1代B', value: 2
                }],

                ratio: 0,
                ratios: [
                    { text: '20%, 10%, 5%', value: 0 },
                    { text: '30%, 20%, 10%', value: 1 },
                ],

                res: [ 0, 0, 0 ]
            }
        },
        methods: {
            getRatio() {
                if (this.ratio == 0) { return [ 0.2, 0.1, 0.05] }
                return [ 0.3, 0.2, 0.1 ]
            },
            numResult() {
                const r = this.getRatio()
                this.res = this.$refs.recommendBonusTree.numResult(this.user, r)
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


